<template>
  <div>
    <nav-bar>
      <template #left>
        <div class="back" @click="regBack()"></div>
      </template>
    </nav-bar>
    <div class="InputComp">
      <div class="form">
        <h2>新用户注册</h2>
        <div>
          <input
            class="email"
            type="email"
            placeholder="邮箱"
            v-model="email"
          />
        </div>
        <div>
          <input
            class="password"
            type="password"
            placeholder="密码"
            v-model="password"
          />
        </div>
        <div>
          <button-comp class="register" @click.native="register()">
            <template #content>注册</template>
          </button-comp>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import ButtonComp from "components/common/input/ButtonComp.vue";

import { register } from "network/user";
import { login_check } from "common/utils";
export default {
  name: "RegisterView",
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    register() {
      if (!login_check.email.test(this.email.trim())) {
        return this.$toast.show("邮箱格式不正确");
      }
      if (!login_check.password.test(this.password.trim())) {
        return this.$toast.show("密码格式不正确,密码只能是6~12位");
      }
      register(this.email, this.password).then((res) => {
        if (res.code === "ERROR") {
          return this.$toast.show(res.msg);
        }
        this.$toast.show(res.msg);
        setTimeout(() => {
          this.regBack();
        }, 1000);
      });
    },
    regBack() {
      this.$router.back();
    },
  },
  components: {
    NavBar,
    ButtonComp,
  },
};
</script>

<style scoped>
.InputComp {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  /* height: calc(100vh - 44px - 49px); */
  height: calc(100vh - 6.59670vh - 7.34632vh);
}
.InputComp .form {
  width: 80%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-evenly;
  height: 40vh;
  min-height: calc(44 * 3px);
}
.email,
.password {
  width: 100%;
  height: 44px;
  outline-color: var(--color-tint);
  border-radius: 8px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.register {
  width: 100%;
  height: 44px;
  outline-color: var(--color-tint);
  border-radius: 10px;
  padding: 5px 10px;
  background-color: var(--color-tint);
  color: #fff;
}
.back {
  display: block;
  width: 44px;
  height: 44px;
  background-image: url(assets/img/common/back.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
</style>